<template>
    <div class="music paddingTop50">
      <v-scroller title="" type="music" :items="music_Chinese" ratingFlag=true>
        <router-link class="head-rt" slot="loadMore" :to="{name: 'musicMore', params:{musicList: music_Chinese, title: ''}}"> <i class="fa fa-angle-right"></i></router-link>
      </v-scroller>
      <v-scroller title="" type="music" :items="music_Usa" ratingFlag=true>
        <router-link class="head-rt" slot="loadMore" :to="{name: 'musicMore', params:{musicList: music_Usa, title: ''}}"> <i class="fa fa-angle-right"></i></router-link>
      </v-scroller>
      <v-scroller title="" type="music" :items="music_JapanKorea" ratingFlag=true>
        <router-link class="head-rt" slot="loadMore" :to="{name: 'musicMore', params:{musicList: music_JapanKorea, title: ''}}"> <i class="fa fa-angle-right"></i></router-link>
      </v-scroller>
      <v-scroller title="" type="music" :items="music_Hot" ratingFlag=true>
        <router-link class="head-rt" slot="loadMore" :to="{name: 'musicMore', params:{musicList: music_Hot, title: ''}}"> <i class="fa fa-angle-right"></i></router-link>
      </v-scroller>
      <div class="m-box">
        <div class="title">
          <i class="title-lf"></i>
          <h3></h3>
          <a class="title-rt colorGreen"> ></a>
        </div>
        <div class="content">
          <div class="content-detail comment-lf">
            <div class="detail-lf">
              <h3> | 9.2</h3>
              <p>9.2</p>
              <div class="comment-content"><span></span>  <span></span></div>
            </div>
            <div class="detail-rt"></div>
          </div>
        </div>
      </div>
      <v-scroller title="" type="music" :items="sliceThree(music_Usa)" marginLeftFlag=true ratingFlag=true>
        <div class="content" slot="promItem">
          <ul class="scroll-items">
            <li class="marginLeft3" v-for="(item, index) in sliceThree(music_Hot)">
              <router-link class="movie-box" :to="{name: 'musicDetail', params:{item: item}}">
                <div class="item-poster" :style="'background:' + 'url(' + item.image + ')'"></div>
                <span class="item-title">{{item.title}}</span>
                <v-rating :rating="item.rating"></v-rating>
              </router-link>
            </li>
          </ul>
        </div>
        <router-link class="lookMore" slot="lookMore" :to="{name: 'interestedMore', params: {typeValue: 'music', data: music_Usa}}"> <i class="fa fa-angle-right colorGreen"></i></router-link>
      </v-scroller>
      <div class="m-box contentAfterN">
        <div class="title">
          <i class="title-lf"></i>
          <h3></h3>
          <a class="title-rt colorGreen"> ></a>
        </div>
        <div class="content" v-for="n in 3">
          <div class="content-detail comment-lf">
            <div class="detail-lf">
              <h3> | 9.2</h3>
              <p>9.2</p>
              <div class="comment-content"><span></span>  <span></span></div>
            </div>
            <div class="detail-rt"></div>
          </div>
        </div>
      </div>
      <div class="m-box">
        <div class="title">
          <i class="title-lf"></i>
          <h3></h3>
        </div>
        <div class="content">
          <ul class="filter">
            <li class="filter-item">
              <a></a>
            </li>
            <li class="filter-item">
              <a></a>
            </li>
            <li class="filter-item">
              <a></a>
            </li>
            <li class="filter-item">
              <a></a>
            </li>
          </ul>
          <ul class="filter">
            <li class="filter-item">
              <a></a>
            </li>
            <li class="filter-item">
              <a></a>
            </li>
            <li class="filter-item">
              <a></a>
            </li>
            <li class="filter-item">
              <a></a>
            </li>
          </ul>
        </div>
      </div>
    </div>
</template>
<style lang="stylus" rel="stylesheet/stylus">
.music
  .item-poster
    height 2rem!important
</style>
<script>
    import rating from '../../../components/rating.vue'
    import scroller from '../../../components/scroller.vue'
    import { mapGetters } from 'vuex'

    export default{
      data () {
        return {}
      },
      components: {
        'v-rating': rating,
        'v-scroller': scroller
      },
      computed: {
        ...mapGetters([
          'music_Chinese',
          'music_Usa',
          'music_JapanKorea',
          'music_Hot'
        ])
      },
      created () {
        this.$store.dispatch('getMusicChinese')
        this.$store.dispatch('getMusicUsa')
        this.$store.dispatch('getMusicJapanKorea')
        this.$store.dispatch('getMusicHot')
      },
      methods: {
        sliceThree: value => value.slice(0, 3)
      }
    }
</script>



